    }

    .imagemDeFaçaParte {
        /* flex-direction: column; */
        /* justify-content: center; */
        height: 50vh;
        width: 25vw;
    }

        /*----------------- Itens do Formulário -----------------*/


    .caixaDoForm {
        width: 20vw;
    }

    #formBotaoRedimensionavel {
        max-width: 20vw;
        min-width: 20vw;  
    }

}
