/* SLIDER  */

.slider-noticias {
  background-color: darkorange;
  margin-bottom: 4em;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.slider-noticias div img {
  width: 450px;
  margin-bottom: -4px;
}

.slider-noticias .titulo {
  font-size: 18px;
  background-color: #eee;
  padding: 10px 20px;
  color: #444;
}

/* POSTAGENS  */

.post-principal {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.post-principal .postagem {
  width: 470px;
  background-color: #eee;
  text-align: center;
  margin: 50px 20px;
  border-radius: 15px;
  box-shadow: 1px 1px 10px 1px #777;
  height: 600px;
}

.post-principal .postagem img {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 450px;
  display: block;
  margin: 10px auto;
}

.post-principal .postagem .titulo {
  font-size: 18px;
  text-align: center;
  padding: 10px 20px;
  margin-top: 10px;
  color: #333;
}

.post-principal .postagem .texto-postagem {
  text-align: justify;
  padding: 10px 20px;
  color: #777;
}

.btnLeia {
  color: #444;
  font-size: 16px;
  padding: 10px 10px;
  font-weight: bold;
  cursor: pointer;
  margin: 10px;
  display: inline-block;
  text-decoration: none;
}

.btnLeia:hover {
  text-decoration: underline;
}

#more {
  display: none;
}

@media (max-width: 450px) {
  .slider-noticias div img {
    width: 100%;
  }
  .post-principal .postagem, .post-principal .postagem img {
    width: 300px !important;
    margin-top: 0 !important;
    transition: 1s;
  }
}

@media (min-width: 700px) {
  .post-principal .postagem, .post-principal .postagem img {
    width: 400px !important;
    margin-top: 0 !important;
    transition: 1s;
  }
}

*{
    font-family: 'Open Sans', sans-serif;
    margin: 0px;
    padding: 0px;
}

/*arrumando div containers*/
.body{
    width: 100%;
    transition: all .9s;
}
/*classes genericas*/
.azul{
    background: rgb(17,56,92);
}

.azul-escuro{
    color: #1B2631;
}

.laranja{
    background-color: rgb(239,123,23);;
}

.flex-row{
    display: flex;
    flex-direction: row;
}

.flex-col{
    display: flex;
    flex-direction: column;
}

.titulo-secao{
    text-align: center;
    font-size: 40px;
}

.poteiro{
    cursor: pointer;
}



/*configurando conteudo superior e seus filhos*/
.conteudo-superior{

    height: 100vh;    
}

.cor-fundo{
    height: 99%;
    box-sizing: border-box;
    align-items: flex-end; 
    flex-wrap: wrap;
}

.separacao{
    height: 1%;
}

/*configurando texto do conteudo superior*/
.formatacao-texto{
    color: white;
    font-size: 16px;   
}

.texto{
    width: 100%;
    margin: 0px auto;
    text-align: center;
}

.texto h2{
    font-size: 28px;
    margin-bottom: 25px;
}

.texto span {
    display: inline-block;
    text-align: center;
    width: 50%;    
}

/*configurando seta para transição*/
div.seta{
    width: 100%;
    margin: 0px auto;
    text-align: center;
    padding-bottom: 10px;
}

.seta .icone-seta{
    fill: white;
    width: 20px;
    height: 20px;
    
}

.seta .fundo-seta{
    display: inline-block;
    width: 40px;
    height: 40px;
}

.seta .fundo-seta:hover{
    background-color: rgb(239,123,23);
    border-radius: 20px;
}

.seta svg{
    margin-top: 11px;
    transition: all .9s;
}

/*aplicando a animação no conteudo superior e no principal*/
.esconder{
    display: none;
}

#principal:checked ~ .body {
    margin-top: -90vh;  
}

.mudar{
   border: 50px solid rgb(7, 29, 49);
}

#principal:checked ~ .body .icone-seta{
    transform: rotate(180deg);
}

/*configurando conteudo principal*/
.conteudo-principal{
    /* height: 100vh; */
}

/*configuarando area do processo seletivo*/
.inscricoes{
    padding: 60px 0px;
    flex-wrap: wrap;
}

.descricao{
    width: 100%;
}

.descricao .barra{
    align-self: center;
    width: 270px;
    height: 5px;
    margin-top: 16px;
    border-radius: 10px;
}

.processos-seletivos{
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 50px;
}

.processos-seletivos .processo{
    height: 360px;
    width: 250px;
    border-radius: 20px;
    margin-bottom: 45px;
    /* transition: all .9s; */
    
}

.processo .cabecalho{
    height: 100px;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    box-sizing: border-box;
    padding-left: 15px;
    padding-top: 10px;
}

.processo h6, .detalhes, .edital{
    font-weight: 900;
    color:#D0D3D4;
    font-family: 'Muli', sans-serif;
    letter-spacing: 0.5px;
    text-decoration: none;
}

.processo a:hover{
    text-decoration: underline; 
}

.processo h2{
    color: #eeeeee;
    font-weight: 300;
    margin-bottom: 13px;
    margin-top: 10px;
}

.info-prova ul{
    list-style: none; 
}

.info-prova li{
    padding: 12px 10px;
    background-color: #FAFAFA;
    color: #7D7D7D;
}

.info-prova .impar{
    background-color: #F5F5F5;
}

.info-prova li.centro{ 
    justify-content: center;
}

li.botao button{
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    font-weight: 700;
    color: #F5F5F5;
    background-color: rgb(17,56,92);
    outline: none;
}

.bordar-redonda{
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

/*configurando area de dicas*/
.container-dicas{
    background-color: #F2F2F2;
    padding-bottom: 20px;
}

.titulo-dica{
    padding-top: 40px; 
}

.titulo-dica p{
    text-align: center;
    color: #6d6d6d;
    margin-top: 15px;
}

.dicas-provas{
    padding-top: 50px;
    justify-content: space-around;
    flex-wrap: wrap;
}

.dicas-provas .dica{
    width: 290px;
    margin-bottom: 50px;
}

.container-icone-pai{
    background-color: white;
    display: inline-block;
    padding: 3px;
    border-radius: 100px;
}

.container-icone{
    background-color: white;
    display: inline-block;
    padding: 15px;
    border-radius: 90px;
    border: 5px solid #F2F2F2;
}

.icone-dica{
    width: 60px;
    height: 60px;
}

.texto-dica{
    align-self: center;
    margin-left: 15px;
}

.texto-dica h3{
    margin-bottom: 9px;
}

.texto-dica p{
    color: #787878;
    font-size: 16px;
}

.edital{
    color: rgb(7, 29, 49);  
}

.edital:hover{
    text-decoration: underline;
}


/*media querie*/
@media(min-width: 500px){
    .processos-seletivos{
        width: 50%
    }

    .inscricoes{
        align-items: center;
    }

    .processos-seletivos .processo{
        width: 350px;
        height: 430px;
    }
    
    .processo .cabecalho{
        height: 120px;
        padding-top: 20px;
    }   

}

@media (min-width: 615px){
    .processo h6, a{
        font-size: 14px;
    }

    .processo h2{
        font-size: 29px
    }


    .info-prova li.botao button{
        padding: 13px 23px;
        border-radius: 25px;
        font-size: 19px;
    }
}

@media (min-width: 1200px){

    .processos-seletivos{
        width: 90%;
    }

    .processo:hover{
        box-shadow: 15px 15px #1B2631;
        position: relative;
        top: -5px;
        transition: top .3s;
    }
}

@media (min-width: 2000px){
    .processos-seletivos{
        
        width: 50%;
    }

    .dicas-provas{
        padding: 50px 700px;
    }
}
